* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.cv-wrapper {
    display: grid;
    grid-template-columns: 1.5fr 2fr;
    grid-template-rows: 100px 1fr 50px;
    grid-gap: 30px;
}

.cv-header {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background-color: #bdc3c7;
    -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
}

.cv-header span {
    margin-left: 15px;
}

.cv-form {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    margin-left: 30px;
    -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
}

.resume {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    margin-right: 30px;
    -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
}

.cv-footer {
    grid-column: 1 / 3;
    grid-row: 3 / 4;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    background-color: #bdc3c7;
    -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.75);
}

.creator a {
    text-decoration: none;
    font-weight: bolder;
    margin-left: 5px;
    color: black;
    transition: all .3s ease-in;
}

.creator a:hover {
    color: rgb(83, 83, 83);
}

.github {
    color: black;
    margin-left: 5px;
    font-size: 20px;
    transition: all .3s ease-in;
}

.github:hover {
    color: rgb(83, 83, 83);
}

@media screen and (max-width: 992px) {
    .cv-wrapper {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 50px 1fr 1fr 50px;
    }

    .cv-header {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
        font-size: 15px;
    }

    .cv-header span {
        margin-left: 5px;
    }

    .cv-form {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
        margin-left: 0;
    }

    .resume {
        grid-column: 1 / 2;
        grid-row: 3 / 4;
        margin-right: 0px;
    }

    .cv-footer {
        grid-column: 1 / 2;
        grid-row: 4 / 5;
        font-size: 15px;
    }
} 